import React, { useState, useEffect } from "react";

import { Button } from "react-bootstrap";
import defaultProductImage from "../assets/open-box.png";
import { addCartOrder, checkOrdered } from "../actions";
import { useSelector, useDispatch } from "react-redux";
import { server } from "../constants/server";
import { Mixpanel } from "../components/Mixpanel";

function CampaignItem(props) {
  const dispatch = useDispatch();
  const server_url = server + "/";
  const [count, setCount] = useState(0);
  const [currentPrice] = useState(props.campaign.product_initial_price);
  const [isOrdered, setOrdered] = useState(false);
  const cart = useSelector((state) => state.cart);

  function timer() {
    var text = "";
    const now = new Date();
    const end_campaign = new Date(props.campaign.end_at);
    var d = end_campaign - now;
    var weekdays = Math.floor(d / 1000 / 60 / 60 / 24 / 7);
    var days = Math.floor(d / 1000 / 60 / 60 / 24 - weekdays * 7);
    var hours = Math.floor(d / 1000 / 60 / 60 - weekdays * 7 * 24 - days * 24);
    var minutes = Math.floor(
      d / 1000 / 60 - weekdays * 7 * 24 * 60 - days * 24 * 60 - hours * 60
    );

    if (days > 0) {
      text = "sisa " + days + " hari lagi";
    } else {
      text = hours + " jam " + minutes + " menit lagi";
    }
    return text;
  }

  let quota_left = 0;
  let tier_quota = 0;
  let tier_price = 0;

  const tieringCase = (
    order,
    initial_price,
    tiering1,
    tiering2,
    tiering3,
    price1,
    price2,
    price3
  ) => {
    var progress_tiering_1 = 0;
    var progress_tiering_2 = 0;
    if (tiering2 > tiering1) {
      if (tiering3 > tiering2) {
        console.log("tier Lv 3");

        if (order < tiering1) {
          //if tiering price for lv 3, order still progress for tier 1
          // setCurrentPrice(price1);
          progress_tiering_1 = 33 - (order / tiering3) * 100;
          progress_tiering_2 = 33;
          quota_left = tiering1 - order;
          tier_quota = tiering1;
          tier_price = price1;
          return (
            <div className="progress" style={{ height: "30px" }}>
              <div
                className="progress-bar bg-warning progress-bar-striped progress-bar-animated  active"
                role="progressbar"
                aria-valuenow="40"
                aria-valuemin="0"
                aria-valuemax="100"
                style={{
                  width: (order / tiering3) * 100 + "%",
                }}
              >
                <span className="bar-text"></span>
              </div>
              <div
                class="progress-bar bg-custom"
                role="progressbar"
                style={{
                  width: progress_tiering_1 + "%",
                  borderRight: "3px solid black",
                }}
                aria-valuenow="30"
                aria-valuemin="0"
                aria-valuemax="100"
              ></div>
              <div
                class="progress-bar bg-custom-2"
                role="progressbar"
                style={{
                  width: progress_tiering_2 + "%",
                  borderRight: "3px solid black",
                }}
                aria-valuenow="30"
                aria-valuemin="0"
                aria-valuemax="100"
              ></div>
            </div>
          );
        } else if (order < tiering2) {
          //if tiering price for lv 3, order still progress for tier 2
          // setCurrentPrice(price1);
          progress_tiering_2 = 66 - (order / tiering3) * 100;
          quota_left = tiering2 - order;
          tier_quota = tiering2;
          tier_price = price2;
          return (
            <div className="progress" style={{ height: "30px" }}>
              <div
                className="progress-bar bg-warning progress-bar-striped progress-bar-animated  active"
                role="progressbar"
                aria-valuenow="40"
                aria-valuemin="0"
                aria-valuemax="100"
                style={{
                  width: (order / tiering3) * 100 + "%",
                }}
              >
                <span className="bar-text"></span>
              </div>

              <div
                class="progress-bar bg-custom-2"
                role="progressbar"
                style={{
                  width: progress_tiering_2 + "%",
                  borderRight: "3px solid black",
                }}
                aria-valuenow="30"
                aria-valuemin="0"
                aria-valuemax="100"
              ></div>
            </div>
          );
        } else {
          //if tiering price for lv 3, order progress for tier 3
          quota_left = tiering3 - order;
          tier_quota = tiering3;
          tier_price = price3;

          return (
            <div className="progress" style={{ height: "30px" }}>
              <div
                className="progress-bar bg-warning progress-bar-striped progress-bar-animated  active"
                role="progressbar"
                aria-valuenow="40"
                aria-valuemin="0"
                aria-valuemax="100"
                style={{
                  width: (order / tiering3) * 100 + "%",
                }}
              >
                <span className="bar-text"></span>
              </div>
            </div>
          );
        }
      } else {
        console.log("tier Lv 2");
        if (order < tiering1) {
          //if tiering price for lv 2, order still progress for tier 1
          // setCurrentPrice(price1);
          progress_tiering_1 = 50 - (order / tiering2) * 100;
          quota_left = tiering1 - order;
          tier_quota = tiering1;
          tier_price = price1;
          console.log(progress_tiering_1);
          return (
            <div className="progress" style={{ height: "30px" }}>
              <div
                className="progress-bar bg-warning progress-bar-striped progress-bar-animated  active"
                role="progressbar"
                aria-valuenow="40"
                aria-valuemin="0"
                aria-valuemax="100"
                style={{
                  width: (order / tiering2) * 100 + "%",
                }}
              >
                <span className="bar-text"></span>
              </div>
              <div
                class="progress-bar bg-custom"
                role="progressbar"
                style={{
                  width: progress_tiering_1 + "%",
                  borderRight: "3px solid black",
                }}
                aria-valuenow="30"
                aria-valuemin="0"
                aria-valuemax="100"
              ></div>
            </div>
          );
        } else {
          //if tiering price for lv 2, order progress for tier 2
          // setCurrentPrice(price1);
          progress_tiering_1 = 0;
          quota_left = tiering2 - order;
          tier_quota = tiering2;
          tier_price = price2;
          return (
            <div className="progress" style={{ height: "30px" }}>
              <div
                className="progress-bar bg-warning progress-bar-striped progress-bar-animated  active"
                role="progressbar"
                aria-valuenow="40"
                aria-valuemin="0"
                aria-valuemax="100"
                style={{
                  width: (order / tiering2) * 100 + "%",
                }}
              >
                <span className="bar-text"></span>
              </div>
            </div>
          );
        }
      }
    } else {
      console.log("tier Lv 1");
      //if tiering price for lv 1, order progress for tier 1
      // setCurrentPrice(price1);
      quota_left = tiering1 - order;
      tier_quota = tiering1;
      tier_price = price1;
      return (
        <div className="progress" style={{ height: "30px" }}>
          <div
            className="progress-bar bg-warning progress-bar-striped progress-bar-animated  active"
            role="progressbar"
            aria-valuenow="40"
            aria-valuemin="0"
            aria-valuemax="100"
            style={{
              width: (order / tiering1) * 100 + "%",
            }}
          >
            <span className="bar-text"></span>
          </div>
        </div>
      );
    }
  };

  useEffect(() => {
    setOrdered(dispatch(checkOrdered(props.campaign.product_id, cart.items)));
  }, [dispatch, props, cart]);

  return (
    <div className="card-campaign">
      {props.campaign.product.image ? (
        <div>
          {" "}
          <img
            className="card-img-top campaign-icon image-fit"
            src={server_url + props.campaign.product.image}
            alt="tidak ada gambar"
            onError={(e) => {
              e.target.src = defaultProductImage;
              e.target.alt = "broken";
            }}
          />
        </div>
      ) : (
        <img
          className="card-img-top campaign-icon image-fit"
          src={defaultProductImage}
          alt="no link"
        />
      )}

      <div className="card-body-campaign">
        <div className="card-text">
          <div className="campaign-product-title">
            {props.campaign.product.name} [<span>{props.campaign.unit}</span>]
          </div>
          <br />
          <div className="product-initial-price">
            Rp {currentPrice.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".")}{" "}
          </div>

          <div className="campaign-container">
            {tieringCase(
              props.campaign.quantity_ordered,
              props.campaign.product_initial_price,
              props.campaign.product_tiering_quota_1,
              props.campaign.product_tiering_quota_2,
              props.campaign.product_tiering_quota_3,
              props.campaign.product_tiering_price_1,
              props.campaign.product_tiering_price_2,
              props.campaign.product_tiering_price_3
            )}

            <div className="product-tiering-price">
              <span className="campaign-tiering-price">
                Rp {tier_price.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".")}
              </span>
              <br />
              <span className="campaign-term">
                Jika {tier_quota} pesanan tercapai
              </span>
            </div>
            <div class="flex-container-campaign">
              <div>
                <span className="text-campaign">
                  {props.campaign.quantity_ordered}
                </span>{" "}
                Pesanan
              </div>
              <div>
                <span className="text-campaign">{quota_left} </span>
                <br />
                Sisa kuota
              </div>
            </div>

            <div className="campaign-duration"> {timer()}</div>
          </div>

          <div>
            {isOrdered ? (
              <Button
                size="sm"
                variant="secondary"
                block
                disabled
                onClick={() => {
                  setOrdered(false);
                }}
              >
                sudah dipilih
              </Button>
            ) : (
              <Button
                size="sm"
                variant="warning"
                className="full-width"
                block
                onClick={() => {
                  dispatch(
                    addCartOrder(
                      props.campaign,
                      props.campaign.id,
                      props.campaign.product.image
                    )
                  );
                  setCount(count + 1);
                  setOrdered(true);
                  Mixpanel.track("click add campaign product to cart button");
                }}
              >
                pesan
              </Button>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}
export default CampaignItem;
